.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* NavBar style */
.navbar {
  background-color: #f5f5f5;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 5px;
  display: flex;
  align-items: center;
}

.navbar a {
  color: #565656;
  font-size: 16px;
  font-weight: bold;
  text-decoration: none;
  margin-right: 20px;
}

.navbar a:first-child {
  margin-left: 10px;
}

.navbar a:hover {
  color: #5C7AEA;
}

.navbar p {
  margin-left: auto;
  margin-right: 2px;
  margin-top: 2px;
  margin-bottom: 2px;
  padding-right: 10px;
  font-size: 13px;
}